  border-width: 40px;
  border-color: #ddd #eee #fff;
  border-radius: 2px;
  margin: 40px;
  padding: 32px;
  display: inline-block;
  position: relative;
  box-shadow: inset 0 0 20px #00000040, 0 20px 10px 5px #00000040;
}

.frame:before {
  inset: -6px;
  box-shadow: inset 0 8px 5px #00000040;
}

.frame:after, .frame:before {
  content: "";
  border-radius: 2px;
  position: absolute;
}

.frame:after {
  inset: -8px;
}

.frame.lean-left {
  transform: perspective(1000px)rotateY(15deg)rotateX(20deg);
}

